@tailwind base;
@tailwind components;
@tailwind utilities;

/* css background-image generated using https://10015.io/tools/css-background-pattern-generator */
.background {
background:
      radial-gradient(at 80% 80%,#f3eeee 25.4%,#0000 26%),
      radial-gradient(at 20% 80%,#f3eeee 25.4%,#0000 26%),
      conic-gradient(from -45deg at 50% 41%,#f3eeee 90deg,#ff47b3 0) 
        15.5px 0;
background-size: 31px 31px;
}


/* css loader generated using https://10015.io/tools/css-loader-generator */
.spinner {
   margin: 40px;
   width: 11.2px;
   height: 11.2px;
   animation: spinner-z355kx 1s infinite linear;
   border-radius: 11.2px;
   box-shadow: 28px 0px 0 0 #fff, 17.4px 21.8px 0 0 #fff, -6.2px 27.2px 0 0 #fff, -25.2px 12px 0 0 #fff, -25.2px -12px 0 0 #fff, -6.2px -27.2px 0 0 #fff, 17.4px -21.8px 0 0 #fff;
}

@keyframes spinner-z355kx {
   to {
      transform: rotate(360deg);
   }
}

.shade{
   -webkit-box-shadow: 3px 3px 10px 3px #464545;
-moz-box-shadow: 3px 3px 10px 3px #464545;
box-shadow: 3px 3px 10px 3px #464545;
}
.backg{
   background: #c70039;
   background: -webkit-linear-gradient(0deg, #c70039 0%, #900c3f 100%);
   background: linear-gradient(0deg, #c70039 0%, #900c3f 100%);
}

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
   0% {
     -webkit-transform: scale(0.5);
             transform: scale(0.5);
   }
   100% {
     -webkit-transform: scale(1);
             transform: scale(1);
   }
 }
 @keyframes scale-up-center {
   0% {
     -webkit-transform: scale(0.5);
             transform: scale(0.5);
   }
   100% {
     -webkit-transform: scale(1);
             transform: scale(1);
   }
 }

 .scale-out-center {
	-webkit-animation: scale-out-center 0.5s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
	        animation: scale-out-center 0.5s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
}

